<template>
  <div class="drugDetails">
    <van-nav-bar
      title="商品详情"
      left-text="返回"
      fixed
      left-arrow
      @click-left="toPage"
      >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <div class="content" v-for="(item,index) in goodsInfo" :key="index" @click="toDetail(index)">
      <img class="img" v-lazy="item.imgURl" />
      <div class="main">
        <p>{{ item.ptitle }} {{ item.spec }}</p>
        <p class="compid">{{ item.verdor }}</p>
        <div class="active">
          <div class="imgTip">
            <!-- <p v-if="item.consell & 1 == 1" style="background-color: #ed2f26;">控销药</p> -->
            <!-- <p v-if="item.rule && item.rule.length > 0">{{ item.rule.length > 0 ? item.rule[0].rulename : '' }}</p> -->
           </div>
          <p>小计：￥{{ (item.rule && item.rule.length > 0 && item.rule[0].rulecode == 1113 ? item.skprice*100 : item.pdprice*100) * (item.num*100) /10000 }}</p>
        </div>
      </div>
      <div class="count">
        <p>x{{ item.num }}</p>
      </div>
    </div>
    <!-- 赠品 -->
    <div class="content" v-if="goodsActiveInfo.length > 0">
      <img class="img" src="..\..\assets\z-logo.png" />
      <div class="main">
        <p>{{ goodsActiveInfo[0].ptitle }}</p>
        <div class="active flag">
          <div class="imgT">
            <img src="..\..\assets\goodsDetails_icon\zengpin.png" />
          </div>
          <p>小计：￥{{ goodsActiveInfo[0].pdprice }}</p>
        </div>
      </div>
      <div class="count">
        <p>x{{ goodsActiveInfo[0].num }}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      goodsInfo: [],
      block: [],
      activeInfo: {}
    }
  },
  created() {
    //  获取详情页商品信息
    this.goodsInfo = JSON.parse(sessionStorage.getItem("placeOrderList"));
    this.goodsActiveInfo = JSON.parse(sessionStorage.getItem("activeOrderList"))
    this.activeInfo = this.goodsActiveInfo[0]
  },
  methods: {
    toPage() {
      this.$router.replace({
        path: '/goodsOrder'
      })
    },
    showBlock(i) {
      if(this.block[i]){
        this.$set(this.block, i, false)
      }else{
         this.$set(this.block, i, true)
      }
    },
    toDetail(index) {
      // this.$router.push({
      //   path: "goodsDetail",
      //   query: {
      //     sku: this.goodsInfo[index].pdno
      //   }
      // });
    }
  }
}
</script>
